@import '../../../_SASS/All';
#about-page{
    i{
        font-size: 2rem;
        color: white;
        position: absolute;
        left: 50%;
        @include transform(translateX(-50%));
        z-index: 100;
        cursor: pointer;
    }
    .fa-chevron-up{
        top: .5rem;
    }
    .fa-chevron-down{
        bottom: .5rem;
    }
    .about-slider{
        @include fullSize();
        .about-slide-container{
            width: 100%;
            height: 400%;
            @include flexBox(column, none, none);
            @include transition(all 1s ease);
            .about-slide{
                width: 100%;
                height: calc(100% / 4);
                @include flexBox(row, none, none);
                overflow: hidden;
                .content{
                    width: 70%;
                    height: 100%;
                    border-right: 200px solid transparent;
                    border-bottom: 1000px solid black;
                    z-index: 25;
                    p{
                        margin: 0 1rem;
                        color:  #fff;
                        font-size: 1.1rem;
                        margin-bottom: 12px;
                    }
                    p:nth-of-type(1){
                        margin-top: 3rem;
                    }
                }
                .about-img{
                    width: 70%;
                    height: 100%;
                    margin-left: -30%;
                    background-size: cover;
                    background-repeat: no-repeat;
                    background-position: center center;
                    z-index: 20;
                }
            }
        }
    }
}